import React, { useState, useContext } from "react";
import "./Login.css";
import {
  initializeLoginFramework,
  handleGoogleSignIn,
  handleSignOut,
  handleFbSignIn,
  createUserWithEmailAndPassword,
  signInWithEmailAndPassword,
} from "./LoginManager";
import { UserContext } from "../../App";
import { useHistory } from "react-router-dom";
import { useLocation } from "react-router-dom/cjs/react-router-dom.min";

const Login = () => {
  // Authentication Area Start
  const [newUser, setNewUser] = useState(false);
  // const [error, setError] = useState("");

  const [user, setUser] = useState({
    isSignedIn: false,
    name: "",
    email: "",
    photo: "",
    password: "",
  });

  initializeLoginFramework();

  const [loggedInUser, setLoggedInUser] = useContext(UserContext);
  const history = useHistory();
  const location = useLocation();

  const { from } = location.state || { from: { pathname: "/destination" } };

  const handleResponse = (res, redirect) => {
    setUser(res);
    setLoggedInUser(res);
    if (redirect) {
      history.replace(from);
    }
  };

  const googleSignIn = () => {
    handleGoogleSignIn().then((res) => {
      handleResponse(res, true);
    });
  };

  const signOut = () => {
    handleSignOut().then((res) => {
      handleResponse(res, false);
    });
  };

  const fbSignIn = () => {
    handleFbSignIn().then((res) => {
      handleResponse(res, true);
    });
  };

  // Authentication Area End

  // Input From Area Start
  const handleBlur = (e) => {
    let isFormValid = true;

    if (e.target.name === "email") {
      isFormValid = /\S+@\S+.\S+/.test(e.target.value);
    }

    if (e.target.name === "password") {
      const isPasswordsValid = e.target.value.length > 6;
      const passwordHasNumber = /\d{1}/.test(e.target.value);
      isFormValid = isPasswordsValid && passwordHasNumber;
    }

    if (isFormValid) {
      const newUserInfo = { ...user };
      newUserInfo[e.target.name] = e.target.value;
      setUser(newUserInfo);
    }
  };

  const handleSubmit = (e) => {
    if (newUser && user.email && user.password) {
      createUserWithEmailAndPassword(user.name, user.email, user.password).then(
        (res) => {
          handleResponse(res, true);
          alert("Successfully Created");
        }
      );
    }

    if (!newUser && user.email && user.password) {
      signInWithEmailAndPassword(user.email, user.password).then((res) => {
        handleResponse(res, true);
        alert("Successfully LogIn");
      });
    }

    e.preventDefault();
  };

  return (
    <>
      <div className="login-container">
        <div className="card-login">
          <form onSubmit={handleSubmit}>
            {newUser ? (
              <h2 className="title"> Create an account</h2>
            ) : (
              <h2 className="title"> Log in</h2>
            )}

            <div className="email-login">
              {newUser && (
                <input
                  type="text"
                  placeholder="Name"
                  name="name"
                  required
                  onBlur={handleBlur}
                />
              )}

              <input
                type="text"
                placeholder="Enter Email"
                name="email"
                required
                onBlur={handleBlur}
              />

              <input
                type="password"
                placeholder="Enter Password"
                name="password"
                required
                onBlur={handleBlur}
              />

              {newUser && (
                <input
                  type="password"
                  placeholder="Confirm Password"
                  name="password"
                  required
                  onBlur={handleBlur}
                />
              )}
            </div>

            {newUser ? (
              ""
            ) : (
              <a className="forget-pass" href="/#">
                Forgot password?
              </a>
            )}

            <input
              type="submit"
              value={newUser ? "Create an account" : "Sign In"}
              className="cta-btn"
            />

            {newUser ? (
              <p className="subtitle">
                Already have an account?{" "}
                <span onClick={() => setNewUser(!newUser)} className="p">
                  {" "}
                  Log In
                </span>
              </p>
            ) : (
              <p className="subtitle">
                Don't have an account?{" "}
                <span onClick={() => setNewUser(!newUser)} className="p">
                  {" "}
                  sign Up
                </span>
              </p>
            )}
          </form>
        </div>

        <p className="span">{newUser.error}</p>

        <p className="or">
          <span>or</span>
        </p>

        <div className="social-login">
          <button className="google-btn">
            <img
              alt="Google"
              src="data:image/svg+xml;base64,"
            />
            <p className="btn-text" onClick={googleSignIn}>
              Continue with Google
            </p>
          </button>
        </div>
      </div>
    </>
  );
};

export default Login;
